import Vue from 'vue'
import VueAxios from 'vue-axios'
import VueAuthenticate from 'vue-authenticate'
import axios from 'axios'
import Router from 'vue-router'
import Reserva from '@/components/commons/Reserva'
import Ok from '@/components/commons/Ok'
import Ko from '@/components/commons/Ko'
import Config from '@/components/commons/Config'
import Login from '@/components/commons/Login'
import Welcome from '@/components/commons/Welcome'
import Pago from '@/components/commons/Pago'
import CircuitoFormulario from '@/components/circuito/Formulario'
import CircuitoDispo from '@/components/circuito/Dispo'
import CircuitoRates from '@/components/circuito/Rates'
import CircuitoDetalle from '@/components/circuito/Detalle'

Vue.use(Router)
Vue.use(VueAxios, axios)
var authenticateData = {
  tokenName: 'access_token',
  baseUrl: 'http://localhost:4000',
  storageType: 'localStorage'
}

Vue.use(VueAuthenticate, authenticateData)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'reserva',
      component: Reserva,
      children: [
        {
          path: '',
          name: 'circuito_formulario',
          component: CircuitoFormulario
        },
        {
          path: 'dispo',
          name: 'circuito_dispo',
          component: CircuitoDispo
        },
        {
          path: 'rates',
          name: 'circuito_rates',
          component: CircuitoRates
        },
        {
          path: 'detalle',
          name: 'circuito_detalle',
          component: CircuitoDetalle
        },

        {
          path: 'ok',
          name: 'ok',
          component: Ok
        },
        {
          path: 'ko',
          name: 'ko',
          component: Ko
        },
        {
          path: 'config',
          name: 'config',
          component: Config
        },
        {
          path: 'login',
          name: 'login',
          component: Login
        },
        {
          path: 'welcome',
          name: 'welcome',
          component: Welcome
        },
        {
          path: 'pago',
          name: 'pago',
          component: Pago
        }
      ]
    }

  ]
})



